
import { Component, ElementRef, ChangeDetectorRef, Inject, Renderer2, forwardRef, ChangeDetectionStrategy,
         ViewEncapsulation, Input, Injector, OnInit, OnChanges, SimpleChanges, ViewChild, HostBinding, HostListener } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { DatalistComponent } from '@farris/ui-datalist';
import { BaseComboComponent } from './base-combo.component';
import { SelectItem } from './combo.interface';
import { ComboService } from './combo.service';
import { filter, take, takeUntil } from 'rxjs/operators';


export const INPUT_COMBO_VALUE_ACCESSOR: any = {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => ComboListComponent),
    multi: true
};
@Component({
    selector: 'farris-combo-list',
    templateUrl: './combo-list.component.html',
    // styleUrls: ['./combo-list.component.scss'],
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush,
    providers: [INPUT_COMBO_VALUE_ACCESSOR, ComboService]
})
export class ComboListComponent extends BaseComboComponent implements OnInit, OnChanges {
    @Input() itemTemplate: any;

    /** æ°æ®ä¸­æ è¯ç¦ç¨çå­æ®µåç§°ï¼é»è®¤ä¸º disabled */
    @Input() disabledField = 'disabled';
    /** åè¡¨ä¸­æ¯å¦æ¾ç¤ºå·²ç¦ç¨çé¡¹ï¼é»è®¤ true */
    @Input() showDisabledItem = true;

    /**
     * æ¾ç¤ºé¢æ¿æ¹å¼ï¼ click | focus, é»è®¤ï¼ click
     */
    @Input() showPanelType: 'click'|'focus' = 'click';
    @ViewChild('dl') dataListRef: DatalistComponent;
    @Input() itemHeight: number;
    @HostBinding('class.f-combo-list') cls = true;
    @HostBinding('class.f-combo-list--tag')
    get clsTag() {
        return this.viewType === 'tag';
    }

    private filterData: any[] = [];
    constructor(
        public el: ElementRef,
        public cdr: ChangeDetectorRef,
        @Inject(DOCUMENT) public document: any,
        public render: Renderer2,
        public comboService: ComboService,
        public injector: Injector
    ) {
        super(el, cdr, document, render, comboService, injector);
    }

    ngOnInit() {
       super.ngOnInit();
       if (this.multiSelect) {
            this.enableCancelSelected = this.multiSelect;
       }
       this.remoteSearch = false;
    }

    ngOnChanges(changes: SimpleChanges): void {
        /**
         * å¦æç´æ¥ç»å®ä¸ä¸ªæ°ç»ä½ä¸ºåè¡¨æ°æ®å¹¶ä¸ data æ¯å¼æ­¥æ°æ®ï¼éè¦æ­¤æ¹æ³ï¼åæ­¥displayTextä¸selectedValues
         */
        if (changes.data && !changes.data.firstChange && !this.uri && !this.nosearch) {
            const datas = changes.data.currentValue;
            let val = this.ngControl ? this.ngControl.value || this.selectedValues : this.selectedValues;

            if (this.selectedValues === undefined || this.selectedValues === null) {
                val = this.displayText;
            }

            if (val !== undefined && val !== null && datas && datas.length > 0) {
                const textArr = this.filterSelections(val, this.data);
                this.displayText = textArr.filter(v => !!v).join(this.separator);
                this.updateSelections(val, this.data);
                this.comboService.selections$.next({action: 'initData'});
            } else {
                this.selections = [];
                this.displayText = '';
            }
        }

        // if (changes.multiSelect && !changes.multiSelect.isFirstChange()) {
        //     this.enableCancelSelected = this.multiSelect;
        // }
    }

    @HostListener('keydown', ['$event'])
    registerKeyboardEvent($event: KeyboardEvent) {

        if (this.multiSelect || (this.readonly || this.disabled))  {
            return;
        }

        if ($event.code === 'ArrowUp' || $event.code === 'ArrowDown' ||
            (!this.editable && !this.readonly && !this.disabled && $event.code === 'Space')) {
            $event.preventDefault();
            $event.stopPropagation();
        }

        const selectItem = (index) => {
            this.comboService.selectItem(data[index], index, false);
            const params = {data: data[index], index};
            if (this.isOpen) {
                this.scrollToCurrentItem(index);
            }
            this.updateSelectValues(params);
        };


        let data = this.filterData && this.filterData.length ? this.filterData : this.data;

        if (!this['_cmbPanel']) {
            data = this.data;
        }

        let _data = [];

        if (this.disabledField) {
            _data = data.filter(n => !this.commonUtils.getValue(this.disabledField, n))
        }

        const getrealIndex = (idx, nextIdx, isNext = true) => {
            if (data[nextIdx]) {
                // å¤æ­ä¸ä¸é¡¹æ¯å¦ç¦ç¨
                const isDisabled = this.commonUtils.getValue(this.disabledField, data[nextIdx]);
                if (isDisabled) {

                    const _idx = _data.findIndex(n => this.commonUtils.getValue(this.idField, n) === this.commonUtils.getValue(this.idField, data[idx]));
                    if (isNext) {
                        nextIdx = _idx + 1;
                    } else {
                        nextIdx = _idx - 1;
                    }
                    if (_data[nextIdx]) {
                        return data.findIndex(n => this.commonUtils.getValue(this.idField, n) === this.commonUtils.getValue(this.idField, _data[nextIdx]));
                    } else {
                        const newIdex = nextIdx > 0 ? 0 : _data.length -1;
                        return data.findIndex(n => this.commonUtils.getValue(this.idField, n) === this.commonUtils.getValue(this.idField, _data[newIdex]) && !this.commonUtils.getValue(this.disabledField, n))
                    }
                }
                return nextIdx;
            }
        }

        const idx = data.findIndex(n => this.commonUtils.getValue(this.idField, n) == this.selectedValues);

        if ($event.code === 'ArrowUp') {  // up
            let prevIdx = idx - 1;
            if (prevIdx < 0) {
                prevIdx = data.length - 1;
            }

            if (this.disabledField) {
                prevIdx = getrealIndex(idx,prevIdx, false);
            }

            selectItem(prevIdx);
        }
        if ($event.code === 'ArrowDown' || ($event.code === 'Space' && !this.editable)) { // down
            let nextIdx = idx + 1;
            if (nextIdx >= data.length) {
                nextIdx = 0;
            }

            if (this.disabledField) {
                nextIdx = getrealIndex(idx,nextIdx);
            }

            selectItem(nextIdx);
        }
    }



    @HostListener('keydown.esc', ['$event'])
    escHandlerEvent($event: KeyboardEvent) {
        if (this.isOpen) {
            this.hide(false);
        }
    }


    protected onSelectionsChange(v: any[]) {
        if (this.uri) {
            if (this.selections && this.selections.length) {
                this.selectedValues = this.selections.map(n => n[this.idField]).join(this.separator);
            }
        }
    }

    private updateSelectValues(item: SelectItem, emitEvent = true) {
        const { data, index } = { ...item };
        this.comboService.selectItem(data, index);
        if (this.dataListRef) {
            this.selectedValues = this.dataListRef.selectedValues;
        }
        if (!this.multiSelect) {
            this.selectedValues = data[this.idField];
        }

        const _displayText = this.comboService.getValue(this.textField);
        if (emitEvent) {
            this.selectChange.emit({ data, index, instance: this, selections: this.selections });
            this.cdr.markForCheck();
            this.onValueChange({ text: _displayText, value: this.selectedValues, selections: this.selections });
        }
    }

    private scrollToCurrentItem(index: number) {
        if (this.dataListRef && this.dataListRef.el) {
            const ulEl = this.dataListRef.el.nativeElement.querySelector('ul.list-group');
            if (ulEl && ulEl.children && ulEl.children[index]) {
                ulEl.children[index].scrollIntoView({block: 'nearest'});
            }
        }
    }

    onSelect(item: SelectItem) {
        this.updateSelectValues(item, true);

        if (!this.multiSelect) {
            this.comboService.isOpen$.next(false);
        }
    }

    onUnSelected(value: SelectItem) {
        const { data } = { ...value };
        this.comboService.unSelectItem(data);
        if (this.dataListRef) {
            this.selectedValues = this.dataListRef.selectedValues;
        }
        // if (!this.multiSelect) {
        //     this.selectedValues = data[this.idField];
        // }
        const _displayText = this.comboService.getValue(this.textField);
        this.cdr.markForCheck();
        this.onValueChange({ text: _displayText, value: this.selectedValues, selections: this.selections });
    }

    onEnter(event: Event) {
        if (this.isOpen) {
            this.hide();
        }
    }

    onKeyup($event) {

    }

    onKeydown($event: KeyboardEvent) {
        if ($event.code === 'Tab') {
            this.hide(false);
        }
    }

    onFocus($event) {
        if (this.showPanelType === 'focus') {
            this.show();
        }
    }

    removeSelectItem($event: MouseEvent, item) {
        if ($event) {
            $event.stopPropagation();
        }
        this.comboService.unSelectItem(item);
        this.selectedValues = this.comboService.getValue(this.idField);
        const _displayText = this.comboService.getValue(this.textField);
        this.cdr.detectChanges();
        this.onValueChange({ text: _displayText, value: this.selectedValues, selections: this.selections });
    }

    protected initDatasChangeAction() {
        this.comboService.data$.subscribe(data => {
            this.filterData = data;


            if (data && data.length && !this.editable) {
              
                if (typeof this.selectedValues === 'boolean') {
                    return;
                }

                if (this.selectedValues) {
                    let idx = -1;
                    let items = [];
                    if (typeof this.selectedValues === 'number' || typeof this.selectedValues === 'boolean') {
                        items = data.filter((n) => {
                            return n[this.idField] === this.selectedValues || n[this.textField] === this.selectedValues;
                        });
                    } else {
                        items = this.selectedValues.split(this.separator).map(val => {
                            return data.find((n, i) => {
                                return n[this.idField] === val || n[this.textField] === val;
                            });
                        });
                    }


                    if (!this.selections || !this.selections.length) {
                        if (items && items.length) {
                            this.selectedValues = items.map(i => {
                                return this.commonUtils.getValue(this.idField, i);
                            }).join(this.separator);
                            this.selections = items;
                        }
                    }
                    
                    idx = data.findIndex(n => {
                        return  this.commonUtils.getValue(this.idField, n)  === this.commonUtils.getValue(this.idField, this.selections[0]) || 
                        this.commonUtils.getValue(this.textField, n) === this.commonUtils.getValue(this.textField, this.selections[0]);
                    });

                    if (idx > -1) {
                        setTimeout(() => {
                            this.scrollToCurrentItem(idx);
                        }, 300);
                    }
                }
              
              
            }

        });
    }


}
